import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			messageList: []
		};
	},
	onShow() {
		this.list();
	},
	methods: {
		list() {
			axios.post('/message/list', {type: 3}).then(({data}) => {
				if (data.code === 200) {
					if (data.data.length) this.messageList = data.data;
				}
			});
		}
	}
}
